"use client";

import React, { useState, useEffect } from "react";
import { FaPlus, FaTrash } from "react-icons/fa";
import SideBar from "../../../components/SideBar/page";
import { Bell } from "lucide-react";
import Image from "next/image";
import { db } from "../../../firebaseConfig";
import { collection, addDoc, getDocs, deleteDoc, doc } from "firebase/firestore";

interface Event {
  id: string; // Firebase document ID
  title: string;
  date: string;
  description: string;
}

const EventManagement = () => {
  const [events, setEvents] = useState<Event[]>([]);
  const [newEvent, setNewEvent] = useState({ title: "", date: "", description: "" });
  const [loading, setLoading] = useState(false);

  // Reference to the "Events" collection
  const eventsCollectionRef = collection(db, "Events");

  // Fetch events from Firebase on component mount
  useEffect(() => {
    const fetchEvents = async () => {
      setLoading(true);
      try {
        const querySnapshot = await getDocs(eventsCollectionRef);
        const eventsData: Event[] = [];
        querySnapshot.forEach((doc) => {
          eventsData.push({ 
            id: doc.id, 
            title: doc.data().title,
            date: doc.data().date,
            description: doc.data().description
          });
        });
        setEvents(eventsData);
      } catch (error) {
        console.error("Error fetching events: ", error);
        alert("Error fetching events");
      } finally {
        setLoading(false);
      }
    };

    fetchEvents();
  }, []);

  const handleAddEvent = async () => {
    if (!newEvent.title || !newEvent.date || !newEvent.description) {
      alert("Please fill in all fields.");
      return;
    }

    try {
      setLoading(true);
      // Add new event to the "Events" collection
      const docRef = await addDoc(eventsCollectionRef, {
        title: newEvent.title,
        date: newEvent.date,
        description: newEvent.description,
        createdAt: new Date().toISOString() // Optional: add timestamp
      });
      
      // Update local state with the new event
      setEvents([...events, {
        id: docRef.id,
        title: newEvent.title,
        date: newEvent.date,
        description: newEvent.description
      }]);
      
      // Reset form
      setNewEvent({ title: "", date: "", description: "" });
    } catch (error) {
      console.error("Error adding event: ", error);
      alert("Error adding event");
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteEvent = async (id: string) => {
    try {
      setLoading(true);
      // Delete event from "Events" collection
      await deleteDoc(doc(db, "Events", id));
      // Update local state
      setEvents(events.filter(event => event.id !== id));
    } catch (error) {
      console.error("Error deleting event: ", error);
      alert("Error deleting event");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 flex">
      <SideBar />
      <div className="flex-1">
        {/* Navbar */}
        <header className="bg-white shadow p-4 flex justify-between items-center">
          <div className="flex items-center gap-4">
            <button className="relative">
              <Bell className="w-6 h-6 text-gray-600" />
            </button>
            <div className="flex items-center gap-2">
              <Image src="/profile.jpg" alt="Admin" width={32} height={32} className="rounded-full" />
              <p className="text-sm font-medium">Admin</p>
            </div>
          </div>
        </header>

        {/* Event Management Section */}
        <main className="p-6">
          <h1 className="text-2xl font-semibold mb-4">Event Management</h1>

          {/* Event Form */}
          <div className="bg-white shadow-md rounded-lg p-4 mb-6">
            <h2 className="text-lg font-semibold mb-3">Add New Event</h2>
            <div className="mb-4">
              <label className="block mb-2">Event Title</label>
              <input
                type="text"
                placeholder="Enter event title"
                className="border rounded-lg p-2 w-full"
                value={newEvent.title}
                onChange={(e) => setNewEvent({ ...newEvent, title: e.target.value })}
              />
            </div>
            <div className="mb-4">
              <label className="block mb-2">Event Date</label>
              <input
                type="date"
                className="border rounded-lg p-2 w-full"
                value={newEvent.date}
                onChange={(e) => setNewEvent({ ...newEvent, date: e.target.value })}
              />
            </div>
            <div className="mb-4">
              <label className="block mb-2">Description</label>
              <textarea
                placeholder="Enter event description"
                className="border rounded-lg p-2 w-full"
                rows={3}
                value={newEvent.description}
                onChange={(e) => setNewEvent({ ...newEvent, description: e.target.value })}
              ></textarea>
            </div>
            <button
              onClick={handleAddEvent}
              disabled={loading}
              className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 flex items-center disabled:bg-blue-400"
            >
              {loading ? "Processing..." : (
                <>
                  <FaPlus className="mr-2" /> Add Event
                </>
              )}
            </button>
          </div>

          {/* Event List */}
          <h2 className="text-xl font-semibold mb-3">Upcoming Events</h2>
          <div className="bg-white shadow-md rounded-lg p-4">
            {loading && events.length === 0 ? (
              <p className="text-gray-500">Loading events...</p>
            ) : events.length === 0 ? (
              <p className="text-gray-500">No events added yet.</p>
            ) : (
              <ul>
                {events.map((event) => (
                  <li key={event.id} className="flex justify-between items-center border-b py-3">
                    <div>
                      <h3 className="font-semibold">{event.title}</h3>
                      <p className="text-sm text-gray-500">{event.date}</p>
                      <p className="text-gray-600">{event.description}</p>
                    </div>
                    <button
                      onClick={() => handleDeleteEvent(event.id)}
                      disabled={loading}
                      className="bg-red-600 text-white px-3 py-1 rounded-lg hover:bg-red-700 flex items-center disabled:bg-red-400"
                    >
                      <FaTrash className="mr-2" /> Delete
                    </button>
                  </li>
                ))}
              </ul>
            )}
          </div>
        </main>
      </div>
    </div>
  );
};

export default EventManagement;